import React from "react";
import Slider from "react-slick";
import "./slider.css"
const Slides=()=>{

    const settings = {
        dots:true,
        infinite: true,
        slidesToShow:6,
        slidesToScroll: 1,
        autoplay: true,
        speed: 2000,
        autoplaySpeed: 2000,
        cssEase: "linear",
        
      };
      return (
        <div>
          <h1 style={{ margin:"5px",fontWeight:"bold" ,fontSize:"50px",color:"#00baf2" ,textAlign:"center"}}>Tech Stacks</h1>
          
          <Slider {...settings}>
            <div className="slide1" >
                <img src="https://www.computerhope.com/jargon/j/javascript.png" alt="im1"/>
            <h3>Javascript</h3>
            </div>
            <div className="slide1">
                <img src="https://play-lh.googleusercontent.com/85WnuKkqDY4gf6tndeL4_Ng5vgRk7PTfmpI4vHMIosyq6XQ7ZGDXNtYG2s0b09kJMw" alt="im2"/>
            <h3>HTML</h3>
            </div>
            <div className="slide1">
            <img src="https://static.javatpoint.com/csspages/images/css-tutorial.png"alt="im3"/>
            <h3>CSS</h3>
            </div>
            <div className="slide1">
            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png" alt="im4"/>
              <h3>React</h3>
            </div>
            <div className="slide1">
            <img src="https://images.opencollective.com/chakra-ui-pro/61bd1dd/logo/256.png" alt="im5"/>
              <h3>Chakra-UI</h3>
            </div>
            <div className="slide1">
            <img src="https://ubunlog.com/wp-content/uploads/2020/04/about-nodejs.png" alt="im6"/>
              <h3>NodeJs</h3>
            </div>
            <div className="slide1">
            <img src="https://styles.redditmedia.com/t5_2rjwd/styles/communityIcon_vekzf3oatrs61.png" alt="im7"/>
              <h3>MongoDB</h3>
            </div>
            <div className="slide1">
            <img src="https://media.trustradius.com/product-logos/h3/JA/T1A83W5H538P.PNG" alt="im8"/>
              <h3>Postman</h3>
            </div>
            <div className="slide1">
            <img src="https://cms-assets.tutsplus.com/uploads/users/34/posts/29738/preview_image/nodejs-expressjs.jpg" alt="im9"/>
              <h3>ExpressJs</h3>
            </div>
            <div className="slide1">
            <img src="data:image/png;base64,"/>
              <h3>Redux</h3>
            </div>
          </Slider>
         
        </div>
      );
    }


    export default Slides


